@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@charset "UTF-8";
/*Обнуление*/
* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: inherit;
}

/*--------------------*/

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 890px;
}
body {
  background-color: #ebecee;
  font-family: "Roboto", sans-serif;
}

.container {
  max-width: 890px;
  min-width: 320px;
  padding: 0px 15px;
  margin: 0px auto;
}

.gallery__row {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    padding-bottom: 30px;
}

.gallery__item {
    position: relative;
    transition: 0.3s all;
    padding: 2px;
}

@media (min-width: 767px) {
    .gallery__item:hover {
        -webkit-transform: scale(0.9);
                transform: scale(0.9);
    }
}

.gallery__preview {
    width: 100%;
    height: 100%;
    object-fit: cover;
    cursor: pointer;
}

@media (max-width: 768px) {
    .gallery__item {
        height: 130px;
    }
}

.gallery__remove {
    font-size: 0;
    position: absolute;
    background: url(/KamilProgram/eqwid-frontend-test/static/media/close.79bd64a4.svg) center/10px 10px no-repeat;
    top: 5px;
    right: 5px;
    height: 20px;
    width: 20px;
    padding: 5px;
    background-color: #dc4040;
    border-radius: 4px;
}

.hide {
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    z-index: -1;
    position: absolute;
    left: 0;
    top: 0;
    overflow: hidden;
}

.lightbox {
    position: fixed;
    transition: 0.5s all;
    top: 0px;
    left: 0;
    width: 100%;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    z-index: 3;
}

.lightbox__overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.7);
}

.lightbox__body {
    padding: 5px;
    width: 100%;
    height: 100%;
    max-height: 100%;
}

.lightbox__body img {
    max-width: 100%;
    max-height: 90vh;
    min-height: 1px;
    padding: 0 35px;
}

.lightbox__content {
    position: relative;
    z-index: 2;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    margin: 15px;
}

.lightbox__navigate {
    font-size: 0;
}

.lightbox__navigate span {
    margin: 10px;
    height: 100%;
    top: 50%;
    position: absolute;
    -webkit-transform: translate(0, -50%);
    transform: translate(0, -50%);
    max-width: 30px;
    max-height: 50px;
    width: 10vw;
    height: 10vw;
    opacity: 0.6;
    cursor: pointer;
}

.lightbox__prev {
    left: 0;
    background: url(/KamilProgram/eqwid-frontend-test/static/media/left.e1553cb5.svg) right no-repeat;
}

.lightbox__next {
    right: 0;
    background: url(/KamilProgram/eqwid-frontend-test/static/media/right.67c7cff6.svg) left no-repeat;
}

.lightbox__open {
    opacity: 1;
    visibility: visible;
}

.drag-drop {
    height: 100px;
    width: 100%;
    border: 3px dashed #000;
    margin-bottom: 20px;
}

.drag-drop__area {
    padding: 5px;
    width: 100%;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    color: #000;
    font-size: 20px;
    font-weight: 700;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    text-align: center;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}
.drag-drop__area input {
    display: none;
}
.load-btn {
    cursor: pointer;
    margin-right: 9px;
    text-decoration: underline;
}

/* //background: url("../../assets/icons/trash.svg") center/20px 20px no-repeat; */
.drop {
    background-color: #fff;
}

.header__btn {
    font-size: 0;
    display: block;
    width: 20px;
    height: 20px;
    cursor: pointer;
}

.header {
    margin-bottom: 20px;
    min-width: 320px;
    background-color: #f9f9fa;
}

.header__body {
    -webkit-align-items: center;
            align-items: center;
    height: 50px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

@media (max-width: 435px) {
    .header__body {
        height: 85px;
        -webkit-flex-direction: column;
                flex-direction: column;
        padding-bottom: 10px;
    }
}

.header__logo img {
    height: 40px;
}

.header__tools {
    -webkit-align-items: center;
            align-items: center;
    font-size: 15px;
    display: -webkit-flex;
    display: flex;
}

.header__add {
    width: 200px;
    margin-right: 15px;
}

@media (max-width: 435px) {
    .header__add {
        margin-right: 20px;
    }
}

.header__edit-btn {
    background: url(/KamilProgram/eqwid-frontend-test/static/media/trash.8b6b7284.svg) center/20px 20px no-repeat;
}
/*Форма добавленния картинки*/
.add-img {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

.add-img__input {
    border: #000 solid 2px;
    padding: 4px;
    border-radius: 7px;
    -webkit-flex: 0 1 70%;
            flex: 0 1 70%;
    color: #8d8d8d;
    font-size: 14px;
    margin-right: 7px;
    min-width: 170px;
    transition: 1s all;
}
.add-img__input:disabled {
    border: #ccc solid 2px;
}

.add-img__btn {
    min-width: 20px;
    background: url(/KamilProgram/eqwid-frontend-test/static/media/plus-circle.6511bfea.svg) center/20px 20px no-repeat;
}

